.highlight {
  font-weight: 700; }

.slideIn {
  animation: slideIn .4s linear 1; }

.slideOut {
  animation: slideOut .4s linear 1; }

.navigation {
  position: relative;
  display: flex; }
  @media (max-width: 900px) {
    .navigation {
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      z-index: 2; } }
  .navigation__list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0; }
    .navigation__list--active {
      display: flex; }
    @media (max-width: 900px) {
      .navigation__list {
        display: none;
        height: 100vh;
        flex-direction: column;
        width: 100%;
        background: #DA0946;
        justify-content: center;
        font-size: 2rem;
        padding: 0; } }
    .navigation__list--active {
      display: flex; }
  .navigation__item {
    list-style-type: none; }
    @media (max-width: 900px) {
      .navigation__item {
        padding: 20px 0; } }
  .navigation__link {
    cursor: pointer;
    text-decoration: none;
    font-size: 1.125rem;
    text-transform: uppercase;
    line-height: 28px;
    color: #ffffff;
    margin: 0 0 0 60px;
    opacity: .8;
    transition: .4s all; }
    @media (max-width: 900px) {
      .navigation__link {
        font-size: 3rem;
        padding: 20px 0;
        margin: 0; } }
    .navigation__link:hover {
      opacity: 1;
